body {
  color: #000;
  min-width: 1024px;
  max-width: 1200px;
  margin: 0 auto;
  font-family: Roboto, sans-serif;
}

:root {
  --text-blue: #00acdc;
}

ul {
  margin: 0;
  padding: 0;
}

a {
  color: #000;
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.header-logo {
  widows: 40px;
  height: 40px;
}

.nav-block {
  align-items: center;
  display: flex;
}

.nav-list {
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-left: 20px;
}

.nav-item:first-of-type {
  margin-left: 0;
  position: relative;
}

.nav-link {
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav-link:hover, .is-active {
  color: var(--text-blue);
}

.is-active:after {
  content: "";
  background-color: var(--text-blue);
  border-radius: 4px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

